import React, { useState } from "react";
import Display from "./Display";

function Image(props) {
  // console.log(props.ele)

  const [value, setValue] = useState(false);

  function Info(e) {
    console.log(e);
    if (value == false) {
      props.setData(e);
      setValue(true);
      let updateddata = props.results.filter((e) => e === props.ele);
      props.setresult(updateddata);
    } else {
      setValue(false);
      props.setData(e);
    }
  }
  return (
    <div className="itm">
      <div>
        <img
          src={props.ele.strMealThumb}
          alt=""
          onClick={() => Info(props.ele.strInstructions)}
        ></img>
      </div>
      <div className="flex-container">
        <h1 className="title">{props.ele.strMeal}</h1>
        <h1 className="title">{props.ele.strArea}</h1>
        {/* <a className="view-btn"  >View Recipe</a>  */}
      </div>
      {/* {data ? <div className="itm-data">{data}</div> : false} */}
    </div>
  );
}

export default Image;
